'use client';

import { useState, useEffect } from 'react';
import { apiClient } from '@/lib/api';
import { useAuth } from '@/contexts/AuthContext';

export default function QuestionsPage() {
    const { token } = useAuth();
    const [questions, setQuestions] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        if (token) {
            fetchCategories();
        }
    }, [token]);

    const fetchCategories = async () => {
        try {
            // We'll fetch categories first as they organize questions
            const response = await apiClient.get('/admin/questions/categories', token!);
            setQuestions(response.data);
        } catch (error) {
            console.error('Failed to fetch question categories:', error);
        } finally {
            setIsLoading(false);
        }
    };

    if (isLoading) return <div>Loading...</div>;

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <h1 className="text-2xl font-bold">Daily Question Categories ‚ùì</h1>
                <button className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700">
                    + Add Category
                </button>
            </div>

            <div className="grid grid-cols-1 gap-6">
                {questions.map((cat: any) => (
                    <div key={cat._id} className="bg-white p-6 rounded-lg shadow border border-gray-200">
                        <div className="flex items-center justify-between mb-4">
                            <div className="flex items-center space-x-4">
                                <span className="text-3xl bg-gray-50 p-2 rounded-lg">{cat.emoji}</span>
                                <div>
                                    <h3 className="text-lg font-bold">{cat.name}</h3>
                                    <p className="text-sm text-gray-500">{cat.description}</p>
                                </div>
                            </div>
                            <span className="bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm font-medium">
                                {cat.questionCount || 0} Questions
                            </span>
                        </div>
                        <div className="flex justify-end space-x-3 pt-4 border-t border-gray-100">
                            <button className="text-sm font-medium text-gray-600 hover:text-gray-900">Manage Questions</button>
                            <button className="text-sm font-medium text-blue-600 hover:text-blue-700">Edit</button>
                            <button className="text-sm font-medium text-red-600 hover:text-red-700">Delete</button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
